.header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 20px 40px;
  background-color: #fff;
  box-shadow: 0px 2px 4px rgba(0, 0, 0, 0.1);
}

.header__logo-container {
  display: flex;
  align-items: center;
  text-decoration: none;
}

.header__logo {
  height: 50px;
  margin-right: 10px;
}

.header__logo-text {
  font-size: 28px;
  font-weight: bold;
  color: #333;
}

.header__profile-link {
  text-decoration: none;
}

.header__button {
  border: none;
  border-radius: 25px;
  padding: 10px 20px;
  font-size: 16px;
  cursor: pointer;
}
.header__button-research {
  background-color: #ff4d4d;
  color: #fff;
  margin-right: 12px;
}
.header__button-research:hover {
  background-color: #e60000;
}
.header__button-team {
  background-color: #4caf50;
  color: #fff;
}

.header__button-team:hover {
  background-color: #3d8b3d;
}
